import { QueryClient, QueryClientProvider } from 'react-query';
import './App.css';
import Home from './Components/Home';
import { Chart } from 'chart.js';

function App() {

  const myClient = new QueryClient();
  return <>
  <QueryClientProvider client={myClient}>
   <Home>
    <Chart/>
   </Home> 
  </QueryClientProvider>
  </>

}

export default App;
