import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Line } from 'react-chartjs-2';
// eslint-disable-next-line no-unused-vars
import Chart from 'chart.js/auto';



function Home() {
  const [allDetails, setAllDetails] = useState([]);
  const [transaction, setTransaction] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');

  useEffect(() => {
    async function getCustomerDetails() {
      const customerDetails = "X-MASTER-KEY"; 
      const response = await axios.get('https://api.jsonbin.io/v3/b/6693e811ad19ca34f8879075', {
        headers: { [customerDetails]: "$2a$10$gyP/kjR6E1QtQJQJmzQ70.PDiGvnuyBxylbsdmbuDlDxilAKivGqO" },
      });
      setAllDetails(response.data.record.customers);
      setTransaction(response.data.record.transactions);
    }

    getCustomerDetails();
  }, []);
  console.log(transaction);

  const getCustomerTransactions = (customerId) => {
    const filteredTransactions = transaction.filter((tran) => tran.customer_id === customerId);
    const totalTransactionAmount = filteredTransactions.reduce((total, tran) => total + tran.amount, 0);
    return totalTransactionAmount;
  };

  const handleViewMore = (customerId) => {
    
    console.log('', customerId);
  };

  const labels = ['January', 'February', 'March', 'April', 'May', 'June', 'July'];
  const data = [1000, 2000, 550, 1300, 750, 1250, 2500 , 875];

  const chartData = {
    labels,
    datasets: [
      {
        label: 'ahmed ali', 
        data, 
        backgroundColor: 'rgba(75, 192, 192, 0.2)', 
        borderColor: 'rgba(75, 192, 192, 1)', 
        borderWidth: 1, 
      },
      {
        label: 'aya elsayed', 
        data: [1300, 550],
        backgroundColor: 'rgba(255, 99, 132, 0.2)', 
        borderColor: 'rgba(255, 99, 132, 1)',
        borderWidth:1,
      },
      {
        label: 'Mina Adel',
        data: [1000,450,300],
        backgroundColor: 'rgba(255, 99, 132, 0.2)',
        borderColor: 'rgba(255, 99, 132, 1)',
        borderWidth:1,
      },
      {
        label: 'Sarah Reda',
        data: [ 775, 1150 ],
        backgroundColor: 'rgba(255, 99, 132, 0.2)',
        borderColor: 'rgba(255, 99, 132, 1)',
        borderWidth:1,
      },
      {
        label: 'Mohamed Sayed',
        data: [1000, 875, 250],
        backgroundColor: 'rgba(255, 99, 132, 0.2)',
        borderColor: 'rgba(255, 99, 132, 1)',
        borderWidth:1,
      }
    ],
  };
  
  const chartOptions = {
    
    scales: {
      yAxes: [
        {
          ticks: {
            beginAtZero: true, 
          },
        },
      ],
    },
  };

  const filteredCustomers = allDetails.filter((customer) => {
    const searchText = searchTerm.toLowerCase();
    return (
      customer.id.toString().toLowerCase().includes(searchText) ||
      customer.name.toLowerCase().includes(searchText)
    );
  });

  return <>
  <div className="container">
      <input type="text" className="form-control my-5" placeholder="search..." onChange={(event) => setSearchTerm(event.target.value)}/>
      <table className="table text-center">
        <thead>
          <tr>
            <th className="w-25">Id</th>
            <th className="w-25">Name</th>
            <th>Total Transactions</th>
          </tr>
        </thead>
        <tbody>
          {filteredCustomers.map((customer, idx) => {

           return<>
           <tr key={idx}>
              <td>{customer.id}</td>
              <td>{customer.name}</td>
              <td>{getCustomerTransactions(customer.id)}</td> 
              <td>{handleViewMore}</td>
            </tr>
            
            </> 
            
            }
            
          )}
        </tbody>
      </table>
    </div>
    <div className="container">
    <Line data={chartData} options={chartOptions} />
    </div>
    
    </>
    
  
}

export default Home;
